<template>
  <div style="height: 100%">
    <!-- <flow-header></flow-header> -->
    <a-layout class="container">
      <a-layout-sider width="300" theme="light" class="select-area">
        <a-row>
          <a-checkable-tag
            v-model="tag.checked0"
            @change="toggleNodeShow0"
            class="tag"
            >工具</a-checkable-tag
          >
          <div align="center">
            <a-list :grid="{ gutter: 8, column: 1 }" v-if="tag.toolShow">
              <a-list-item>
                <a-button-group>
                  <a-button
                    v-for="(tool, index) in field.tools"
                    :key="index"
                    :icon="tool.icon"
                    :type="
                      currentTool.type == tool.type ? 'primary' : 'default'
                    "
                    @click="selectTool(tool.type)"
                  >
                  </a-button>
                </a-button-group>
              </a-list-item>
            </a-list>
          </div>
        </a-row>
        <a-row>
          <a-checkable-tag
            v-model="tag.checked1"
            @change="toggleNodeShow1"
            class="tag"
            >Petri节点</a-checkable-tag
          >

          <div align="center">
            <a-list :grid="{ gutter: 8, column: 2 }" v-if="tag.commonNodeShow">
              <a-list-item
                v-for="(commonNode, index) in field.commonNodes"
                :key="index"
              >
                <div
                  :class="commonNode.className"
                  class="node-item"
                  :type="commonNode.type"
                  belongto="commonNodes"
                >
                  {{ commonNode.nodeName }}
                </div>
              </a-list-item>
            </a-list>
          </div>
        </a-row>
        <a-row>
          <div align="attr">
            <flow-attr
              :plumb="plumb"
              :flowData.sync="flowData"
              :select.sync="currentSelect"
            ></flow-attr>
          </div>
        </a-row>
      </a-layout-sider>
      <a-layout>
        <a-layout-header class="header-option">
          <a-tooltip title="generate" placement="bottom">
            <a-button
              @click="showPetriNet"
              class="header-option-button"
              size="small"
              icon="gateway"
            ></a-button>
          </a-tooltip>
          <a-tooltip title="upload" placement="bottom">
            <a-upload :beforeUpload="loadPetri">
              <a-button
                class="header-option-button"
                size="small"
                icon="upload"
              ></a-button>
            </a-upload>
          </a-tooltip>
          <a-tooltip title="save" placement="bottom">
            <a-button
              @click="saveFlow"
              class="header-option-button"
              size="small"
              icon="save"
            ></a-button>
          </a-tooltip>
          <!-- <a-tooltip title="生成流程图片" placement="bottom">
            <a-button
              @click="exportFlowPicture"
              class="header-option-button"
              size="small"
              icon="picture"
            ></a-button>
          </a-tooltip> -->
          <a-popconfirm
            title="确认要重新绘制吗？"
            placement="bottom"
            okText="确认"
            cancelText="取消"
            @confirm="clear"
          >
            <a-tooltip title="重新绘制" placement="bottom">
              <a-button
                class="header-option-button"
                size="small"
                icon="delete"
              ></a-button>
            </a-tooltip>
          </a-popconfirm>

          <a-tooltip title="退出" placement="bottom">
            <a-button
              @click="exit"
              class="header-option-button"
              size="small"
              icon="logout"
            ></a-button>
          </a-tooltip>
        </a-layout-header>
        <a-layout-content class="content">
          <a-spin tip="Loading..." :spinning="spinning" size="large"> </a-spin>
          <flow-area
            ref="flowArea"
            :browserType="browserType"
            :flowData="flowData"
            :select.sync="currentSelect"
            :selectGroup.sync="currentSelectGroup"
            :plumb="plumb"
            :currentTool="currentTool"
            :placeId="placeId"
            :tranId="tranId"
            @findNodeConfig="findNodeConfig"
            @selectTool="selectTool"
            @saveFlow="saveFlow"
            @placeIdInc="placeIdInc"
            @getShortcut="getShortcut"
            @tranIdInc="tranIdInc"
          >
          </flow-area>

          <vue-context-menu
            :contextMenuData="linkContextMenuData"
            @deleteLink="deleteLink"
          >
          </vue-context-menu>
        </a-layout-content>
      </a-layout>
      <a-layout-sider width="350" theme="light" class="attr-area">
        <algorithm
          :result="result"
          :placeId="placeId"
          :tranId="tranId"
          :flowData="flowData"
          @generateReachGraph="getRes"
          @generateSimphon="getRes"
          @generateEventWait="getRes"
        >
        </algorithm>
      </a-layout-sider>
    </a-layout>
    <a-modal
      :title="'流程设计图_' + flowData.attr.name + '.png'"
      centered
      width="90%"
      :closable="flowPicture.closable"
      :maskClosable="flowPicture.maskClosable"
      :visible="flowPicture.modalVisible"
      okText="下载到本地"
      cancelText="取消"
      @ok="downLoadFlowPicture"
      @cancel="cancelDownLoadFlowPicture"
    >
      <div align="center">
        <img :src="flowPicture.url" />
      </div>
    </a-modal>

    <a-modal title="Generate petri net" v-model="netVisible" @cancel="closeNet">
      <a-input
        v-model.number="placeCount"
        placeholder="Please enter number of place count"
      />
      <br />
      <br />
      <a-input
        v-model.number="tranCount"
        placeholder="Please enter number of tran count"
      />

      <template slot="footer">
        <a-button key="acNet" type="primary" @click="generatePetriNet('ac')">
          AcNet
        </a-button>
        <a-button key="fcNet" type="primary" @click="generatePetriNet('fc')">
          FcNet
        </a-button>
      </template>
    </a-modal>
  </div>
</template>

<script>
import jsplumb from "jsplumb";
import { tools, commonNodes } from "./config/basic-node-config.js";
import { flowConfig } from "./config/args-config.js";
import $ from "jquery";
import "jquery-ui/ui/widgets/draggable";
import "jquery-ui/ui/widgets/droppable";
import "jquery-ui/ui/widgets/resizable";
import html2canvas from "html2canvas";
import canvg from "canvg";
import { ZFSN } from "./util/ZFSN.js";
import FlowArea from "./modules/FlowArea";
import FlowAttr from "./modules/FlowAttr";
// import FlowHeader from "./modules/Header";
import Algorithm from "./modules/Algorithm";
import axios from "axios";
axios.defaults.withCredentials = true;
export default {
  name: "vfd",
  components: {
    jsplumb,
    flowConfig,
    html2canvas,
    canvg,
    FlowArea,
    FlowAttr,
    Algorithm
  },
  mounted() {
    const that = this;
    that.dealCompatibility();
    that.initNodeSelectArea();
    that.initJsPlumb();
    that.listenShortcut(); 
    that.initFlow();
    that.listenPage();
  },
  data() {
    return {
      tag: {
        checked0: true,
        checked1: true,
        checked2: true,
        checked3: true,
        toolShow: true,
        commonNodeShow: true
      },
      browserType: 3,
      plumb: {},
      field: {
        tools,
        commonNodes
      },
      flowData: {
        nodeList: [],
        linkList: [],
        attr: {
          name: "",
          des: "",
          createTime: ""
        },
        status: flowConfig.flowStatus.CREATE
      },
      currentTool: {
        type: "drag",
        icon: "drag",
        name: "拖拽"
      },
      currentSelect: {},
      currentSelectGroup: [],
      activeShortcut: true, 
      linkContextMenuData: flowConfig.contextMenu.link,
      flowPicture: {
        url: "",
        modalVisible: false,
        closable: false,
        maskClosable: false
      },
      placeId: 1,
      tranId: 1,
      result: "",
      netVisible: false,
      placeCount: null,
      tranCount: null,
      spinning: false
    };
  },
  methods: {
    toggleNodeShow0(flag) {
      if (!flag) {
        this.tag.toolShow = false;
      } else {
        this.tag.toolShow = true;
      }
    },
    toggleNodeShow1(flag) {
      if (!flag) {
        this.tag.commonNodeShow = false;
      } else {
        this.tag.commonNodeShow = true;
      }
    },
    getBrowserType() {
      let userAgent = navigator.userAgent;
      let isOpera = userAgent.indexOf("Opera") > -1;
      if (isOpera) {
        return 1;
      }
      if (userAgent.indexOf("Firefox") > -1) {
        return 2;
      }
      if (userAgent.indexOf("Chrome") > -1) {
        return 3;
      }
      if (userAgent.indexOf("Safari") > -1) {
        return 4;
      }
      if (
        userAgent.indexOf("compatible") > -1 &&
        userAgent.indexOf("MSIE") > -1 &&
        !isOpera
      ) {
        alert("IE浏览器支持性较差，推荐使用Firefox或Chrome");
        return 5;
      }
      if (userAgent.indexOf("Trident") > -1) {
        alert("Edge浏览器支持性较差，推荐使用Firefox或Chrome");
        return 6;
      }
    },
    dealCompatibility() {
      const that = this;

      that.browserType = that.getBrowserType();
      if (that.browserType == 2) {
        flowConfig.shortcut.scaleContainer = {
          code: 16,
          codeName: "SHIFT(chrome下为ALT)",
          shortcutName: "画布缩放"
        };
      }
    },
    initJsPlumb() {
      const that = this;

      that.plumb = jsPlumb.getInstance(flowConfig.jsPlumbInsConfig);

      that.plumb.bind("beforeDrop", function(info) {
        let sourceId = info.sourceId;
        let targetId = info.targetId;

        if (sourceId.charAt(0) == targetId.charAt(0)) {
          that.$message.info("Nodes of the same type cannot be connected.");
          return false;
        }
        return true;
      });

      that.plumb.bind("connection", function(conn, e) {
        let connObj = conn.connection.canvas;
        let o = {},
          id,
          label;
        if (
          that.flowData.status == flowConfig.flowStatus.CREATE ||
          that.flowData.status == flowConfig.flowStatus.MODIFY
        ) {
          id = "link-" + ZFSN.getId();
          label = "";
        } else if (that.flowData.status == flowConfig.flowStatus.LOADING) {
          let l = that.flowData.linkList[that.flowData.linkList.length - 1];
          id = l.id;
          label = l.label;
        }
        connObj.id = id;
        o.type = "link";
        o.id = id;
        o.sourceId = conn.sourceId;
        o.targetId = conn.targetId;
        o.label = label;
        o.cls = {
          linkType: "Straight",
          linkColor: "#2a2929",
          linkThickness: 2
        };
        $("#" + id).bind("contextmenu", function(e) {
          that.showLinkContextMenu(e);
          that.currentSelect = that.flowData.linkList.filter(
            l => l.id == id
          )[0];
        });
        $("#" + id).bind("click", function(e) {
          let event = window.event || e;
          event.stopPropagation();
          that.currentSelect = that.flowData.linkList.filter(
            l => l.id == id
          )[0];
        });
        if (that.flowData.status != flowConfig.flowStatus.LOADING)
          that.flowData.linkList.push(o);
      });

      that.plumb.importDefaults({
        ConnectionsDetachable: flowConfig.jsPlumbConfig.conn.isDetachable
      });

      ZFSN.consoleLog(["实例化JsPlumb成功..."]);
    },
    initNodeSelectArea() {
      $(document).ready(function() {
        $(".node-item").draggable({
          opacity: flowConfig.defaultStyle.dragOpacity,
          helper: "clone",
          cursorAt: {
            top: 16,
            left: 60
          },
          containment: "window",
          revert: "invalid"
        });
        ZFSN.consoleLog(["初始化节点选择列表成功..."]);
      });
    },
    listenShortcut() {
      const that = this;
      document.onkeydown = function(e) {
        let event = window.event || e;

        if (!that.activeShortcut) return;
        let key = event.keyCode;

        switch (key) {
          case flowConfig.shortcut.multiple.code:
            that.$refs.flowArea.rectangleMultiple.flag = true;
            break;
          case flowConfig.shortcut.dragContainer.code:
            that.$refs.flowArea.container.dragFlag = true;
            break;
          case flowConfig.shortcut.scaleContainer.code:
            that.$refs.flowArea.container.scaleFlag = true;
            break;
          case flowConfig.shortcut.dragTool.code:
            that.selectTool("drag");
            break;
          case flowConfig.shortcut.connTool.code:
            that.selectTool("connection");
            break;
          case flowConfig.shortcut.zoomInTool.code:
            that.selectTool("zoom-in");
            break;
          case flowConfig.shortcut.zoomOutTool.code:
            that.selectTool("zoom-out");
            break;
          case 37:
            that.moveNode("left");
            break;
          case 38:
            that.moveNode("up");
            break;
          case 39:
            that.moveNode("right");
            break;
          case 40:
            that.moveNode("down");
            break;
        }
      };

      document.onkeyup = function(e) {
        let event = window.event || e;

        let key = event.keyCode;
        if (key == flowConfig.shortcut.dragContainer.code) {
          that.$refs.flowArea.container.dragFlag = false;
        } else if (key == flowConfig.shortcut.scaleContainer.code) {
          event.preventDefault();
          that.$refs.flowArea.container.scaleFlag = false;
        } else if (key == flowConfig.shortcut.multiple.code) {
          that.$refs.flowArea.rectangleMultiple.flag = false;
        }
      };

      ZFSN.consoleLog(["初始化快捷键成功..."]);
    },
    getShortcut() {
      this.activeShortcut = true;
    },
    listenPage() {
      window.onbeforeunload = function(e) {
        e = e || window.event;
        if (e) {
          e.returnValue = "关闭提示";
        }
        return "关闭提示";
      };
    },
    initFlow() {
      const that = this;

      axios.interceptors.request.use(
        function(config) {
          // Do something before request is sent
          // 数据请求前，将组件库的 Toast轻提示打开
          that.spinning = true;
          return config;
        },
        function(error) {
          // Do something with request error
          return Promise.reject(error);
        }
      );
      // Add a response interceptor
      axios.interceptors.response.use(
        function(response) {
          // Do something with response data
          //请求得到数据之后，关闭loading
          that.spinning = false;
          return response;
        },
        function(error) {
          // Do something with response error
          that.spinning = false;
          return Promise.reject(error);
        }
      );
      Date.prototype.format = function(fmt) {
        var o = {
          "M+": this.getMonth() + 1, //月份
          "d+": this.getDate(), //日
          "h+": this.getHours(), //小时
          "m+": this.getMinutes(), //分
          "s+": this.getSeconds(), //秒
          "q+": Math.floor((this.getMonth() + 3) / 3), //季度
          S: this.getMilliseconds() //毫秒
        };
        if (/(y+)/.test(fmt)) {
          fmt = fmt.replace(
            RegExp.$1,
            (this.getFullYear() + "").substr(4 - RegExp.$1.length)
          );
        }
        for (var k in o) {
          if (new RegExp("(" + k + ")").test(fmt)) {
            fmt = fmt.replace(
              RegExp.$1,
              RegExp.$1.length == 1
                ? o[k]
                : ("00" + o[k]).substr(("" + o[k]).length)
            );
          }
        }
        return fmt;
      };
      if (that.flowData.status == flowConfig.flowStatus.CREATE) {
        that.flowData.attr.name = "flow-" + ZFSN.getId();
        that.flowData.attr.createTime = new Date().format(
          "yyyy-MM-dd hh:mm:ss"
        );
      } else {
        that.loadFlow();
      }
      ZFSN.consoleLog(["初始化流程图成功..."]);
    },
    loadFlow(json) {
      const that = this;
      if (that.flowData.nodeList.length) {
        this.$message.error("请先清空当前数据");
        return;
      }
      let loadData = JSON.parse(json);
      console.log(loadData);
      that.placeId = loadData.attr.maxPlaceId;
      that.tranId = loadData.attr.maxTranId;
      that.flowData.attr.name = loadData.attr.name;
      that.flowData.config = loadData.config;
      that.flowData.status = flowConfig.flowStatus.LOADING;
      let nodeList = loadData.nodeList;
      nodeList.forEach(function(node, index) {
        that.flowData.nodeList.push(node);
      });
      that.plumb.batch(function() {
        that.$nextTick(() => {
          let linkList = loadData.linkList;
          linkList.forEach(function(link, index) {
            that.flowData.linkList.push(link);
            let conn = that.plumb.connect({
              source: link.sourceId,
              target: link.targetId,
              anchor: flowConfig.jsPlumbConfig.anchor.default,
              connector: [
                "Straight",
                {
                  gap: 5,
                  cornerRadius: 8,
                  alwaysRespectStubs: true
                }
              ],
              paintStyle: {
                stroke: "#2a2929",
                strokeWidth: 2
              }
            });
            if (link.weight) {
              conn.setLabel({
                label: link.weight + "",
                cssClass: "linkWeight"
              });
            }
          });
          that.currentSelect = {};
          that.currentSelectGroup = [];
          that.flowData.status = flowConfig.flowStatus.MODIFY;
        });
      }, true);
      let canvasSize = that.computeCanvasSize();
      that.$refs.flowArea.container.pos = {
        top: -canvasSize.minY + 100,
        left: -canvasSize.minX + 100
      };
    },
    findNodeConfig(belongto, type, callback) {
      let node = commonNodes.filter(n => n.type == type);
      if (node && node.length >= 0) node = node[0];
      callback(node);
    },
    selectTool(type) {
      let tool = tools.filter(t => t.type == type);
      if (tool && tool.length >= 0) this.currentTool = tool[0];

      switch (type) {
        case "drag":
          this.changeToDrag();
          break;
        case "connection":
          this.changeToConnection();
          break;
        case "zoom-in":
          this.changeToZoomIn();
          break;
        case "zoom-out":
          this.changeToZoomOut();
          break;
      }
    },
    changeToDrag() {
      const that = this;

      that.flowData.nodeList.forEach(function(node, index) {
        let f = that.plumb.toggleDraggable(node.id);
        if (!f) {
          that.plumb.toggleDraggable(node.id);
        }
        that.plumb.unmakeSource(node.id);
        that.plumb.unmakeTarget(node.id);
      });
    },
    changeToConnection() {
      const that = this;

      that.flowData.nodeList.forEach(function(node, index) {
        let f = that.plumb.toggleDraggable(node.id);
        if (f) {
          that.plumb.toggleDraggable(node.id);
        }
        if (node.type != "x-lane" && node.type != "y-lane") {
          that.plumb.makeSource(
            node.id,
            flowConfig.jsPlumbConfig.makeSourceConfig
          );
          that.plumb.makeTarget(
            node.id,
            flowConfig.jsPlumbConfig.makeTargetConfig
          );
        }
      });

      that.currentSelect = {};
      that.currentSelectGroup = [];
    },
    changeToZoomIn() {
      console.log("切换到放大工具");
    },
    changeToZoomOut() {
      console.log("切换到缩小工具");
    },
    checkFlow() {
      const that = this;
      let nodeList = that.flowData.nodeList;

      if (nodeList.length <= 0) {
        this.$message.error("流程图中无任何节点！");
        return false;
      }
      return true;
    },

    loadPetri(file, fileList) {
      let that = this;
      let reader = new FileReader();
      reader.readAsText(file, "UTF-8");
      reader.onload = function() {
        that.loadFlow(this.result);
      };
      return false;
    },
    saveFlow() {
      const that = this;
      let petriObj = Object.assign({}, that.flowData);
      petriObj.linkList.forEach(link => delete link.cls);
      petriObj.attr.maxPlaceId = that.placeId;
      petriObj.attr.maxTranId = that.tranId;
      if (!that.checkFlow()) return;
      petriObj.status = flowConfig.flowStatus.SAVE;

      let petriJson = JSON.stringify(petriObj);

      const data = `\uFEFF${petriJson}`;
      const blob = new Blob([data], { type: "text,charset=UTF-8" });
      const downloadElement = document.createElement("a");
      // 创建下载链接
      const href = window.URL.createObjectURL(blob);
      downloadElement.href = href;
      // 下载文件名
      downloadElement.download = `${petriObj.attr.name}.txt`;
      document.body.appendChild(downloadElement);
      downloadElement.click();
      // 移除元素
      document.body.removeChild(downloadElement);
      // 释放blob对象
      window.URL.revokeObjectURL(href);

      this.$message.success("保存数据成功！");
    },
    exportFlowPicture() {
      const that = this;

      if (!that.checkFlow()) return;

      let $Container = that.$refs.flowArea.$el.children[0],
        svgElems = $($Container).find('svg[id^="link-"]'),
        removeArr = [];

      svgElems.each(function(index, svgElem) {
        let linkCanvas = document.createElement("canvas");
        let canvasId = "linkCanvas-" + ZFSN.getId();
        linkCanvas.id = canvasId;
        removeArr.push(canvasId);

        let svgContent = svgElem.outerHTML.trim();
        canvg(linkCanvas, svgContent);
        if (svgElem.style.position) {
          linkCanvas.style.position += svgElem.style.position;
          linkCanvas.style.left += svgElem.style.left;
          linkCanvas.style.top += svgElem.style.top;
        }
        $($Container).append(linkCanvas);
      });

      let canvasSize = that.computeCanvasSize();

      let pbd = flowConfig.defaultStyle.photoBlankDistance;
      let offsetPbd = ZFSN.div(pbd, 2);

      html2canvas($Container, {
        width: canvasSize.width + pbd,
        height: canvasSize.height + pbd,
        scrollX: -canvasSize.minX + offsetPbd,
        scrollY: -canvasSize.minY + offsetPbd,
        logging: false,
        onclone: function(args) {
          removeArr.forEach(function(id, index) {
            $("#" + id).remove();
          });
        }
      }).then(canvas => {
        let dataURL = canvas.toDataURL("image/png");
        that.flowPicture.url = dataURL;
        that.flowPicture.modalVisible = true;
      });
    },
    downLoadFlowPicture() {
      const that = this;
      let alink = document.createElement("a");
      let alinkId = "alink-" + ZFSN.getId();
      alink.id = alinkId;
      alink.href = that.flowPicture.url;
      alink.download = "流程设计图_" + that.flowData.attr.name + ".png";
      alink.click();
    },
    cancelDownLoadFlowPicture() {
      this.flowPicture.url = "";
      this.flowPicture.modalVisible = false;
    },
    computeCanvasSize() {
      const that = this;
      let nodeList = Object.assign([], that.flowData.nodeList),
        minX = nodeList[0].x,
        minY = nodeList[0].y,
        maxX = nodeList[0].x + nodeList[0].width,
        maxY = nodeList[0].y + nodeList[0].height;
      nodeList.forEach(function(node, index) {
        minX = Math.min(minX, node.x);
        minY = Math.min(minY, node.y);
        maxX = Math.max(maxX, node.x + node.width);
        maxY = Math.max(maxY, node.y + node.height);
      });
      let canvasWidth = maxX - minX;
      let canvasHeight = maxY - minY;
      return {
        width: canvasWidth,
        height: canvasHeight,
        minX: minX,
        minY: minY,
        maxX: maxX,
        maxY: maxY
      };
    },
    clear() {
      const that = this;
      that.flowData.nodeList.forEach(function(node, index) {
        that.plumb.remove(node.id);
      });
      that.placeId = 1;
      that.tranId = 1;
      that.currentSelect = {};
      that.currentSelectGroup = [];
      that.flowData.nodeList = [];
      that.flowData.linkList = [];
    },

    exit() {
      alert("退出流程设计器...");
    },
    showLinkContextMenu(e) {
      let event = window.event || e;

      event.preventDefault();
      event.stopPropagation();
      $(".vue-contextmenuName-flow-menu").css("display", "none");
      $(".vue-contextmenuName-node-menu").css("display", "none");
      let x = event.clientX;
      let y = event.clientY;
      this.linkContextMenuData.axis = { x, y };
    },
    deleteLink() {
      const that = this;
      let sourceId = that.currentSelect.sourceId;
      let targetId = that.currentSelect.targetId;
      that.plumb.deleteConnection(
        that.plumb.getConnections({
          source: sourceId,
          target: targetId
        })[0]
      );
      let linkList = that.flowData.linkList;
      linkList.splice(
        linkList.findIndex(
          link => link.sourceId == sourceId || link.targetId == targetId
        ),
        1
      );
      that.currentSelect = {};
    },
    moveNode(type) {
      const that = this;

      let m = flowConfig.defaultStyle.movePx,
        isX = true;
      switch (type) {
        case "left":
          m = -m;
          break;
        case "up":
          m = -m;
          isX = false;
          break;
        case "right":
          break;
        case "down":
          isX = false;
      }

      if (that.currentSelectGroup.length > 0) {
        that.currentSelectGroup.forEach(function(node, index) {
          if (isX) {
            node.x += m;
          } else {
            node.y += m;
          }
        });
        that.plumb.repaintEverything();
      } else if (that.currentSelect.id) {
        if (isX) {
          that.currentSelect.x += m;
        } else {
          that.currentSelect.y += m;
        }
        that.plumb.repaintEverything();
      }
    },
    placeIdInc() {
      this.placeId += 1;
    },
    tranIdInc() {
      this.tranId += 1;
    },
    getRes(methodName, params) {
      const that = this;
      let petriData = Object.assign({}, that.flowData);
      petriData.linkList.forEach(link => delete link.cls);
      petriData.attr.maxPlaceId = that.placeId - 1;
      petriData.attr.maxTranId = that.tranId - 1;
      if (!that.checkFlow()) return;
      petriData.status = flowConfig.flowStatus.SAVE;
      console.log(petriData);
      axios({
        contentType: "application/json;charset=UTF-8",
        method: "post",
        url: flowConfig.serverConfig.algUrl,
        data: {
          methodName,
          nodeList: petriData.nodeList,
          linkList: petriData.linkList,
          status: petriData.status,
          attr: petriData.attr,
          params
        }
      }).then(function(response) {
        let resData = response.data;
        if (resData.success) {
          that.result = resData.data;
          console.log(that.result);
        } else {
          that.$message.error("server has error:" + resData.errorMessage);
        }
      });
    },
    showPetriNet() {
      this.netVisible = true;
    },
    generatePetriNet(netType) {
      let that = this;
      this.netVisible = false;
      let placeCount = this.placeCount;
      let tranCount = this.tranCount;
      axios({
        contentType: "application/json;charset=UTF-8",
        method: "post",
        url: flowConfig.serverConfig.generateUrl,
        data: {
          placeCount,
          tranCount,
          netType
        }
      }).then(function(response) {
        let resData = response.data;
        if (resData.success) {
          that.loadFlow(resData.data);
        } else {
          that.$message.error("server has error:" + resData.errorMessage);
        }
      });
    },
    closeNet() {
      this.netVisible = false;
    }
  }
};
</script>

<style lang="scss">
@import "./style/flow-designer.scss";
</style>

import { Logger } from 'html2canvas/dist/types/core/logger'



