<template>
  <div>
    <a-tabs size="small" defaultActiveKey="flow-attr" :activeKey="activeKey">
      <a-tab-pane key="flow-attr">
        <span slot="tab">
          <a-icon type="cluster" />
          Petri属性
        </span>
        <a-form layout="horizontal">
          <a-form-item
            label="名称"
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
          >
            <a-input :value="flow.attr.name" @change="petriNameChange" />
          </a-form-item>
        </a-form>
        <a-form layout="horizontal">
          <a-form-item
            label="创建时间"
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
          >
            <a-input :value="flow.attr.createTime" disabled />
          </a-form-item>
        </a-form>
        <a-form layout="horizontal">
          <a-form-item
            label="描述"
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
          >
            <a-input :value="flow.attr.des" @change="petriDesChange" />
          </a-form-item>
        </a-form>
      </a-tab-pane>
      <a-tab-pane key="node-attr">
        <span slot="tab">
          <a-icon type="profile" />
          库所属性
        </span>
        <template
          v-if="currentSelect.type && currentSelect.type.indexOf('place') != -1"
        >
          <a-form layout="horizontal">
            <a-form-item
              label="类型"
              :label-col="formItemLayout.labelCol"
              :wrapper-col="formItemLayout.wrapperCol"
            >
              <a-tag color="purple">{{ currentSelect.type }}</a-tag>
            </a-form-item>
            <a-form-item
              label="id"
              :label-col="formItemLayout.labelCol"
              :wrapper-col="formItemLayout.wrapperCol"
            >
              <a-input :value="currentSelect.id" disabled />
            </a-form-item>
            <a-form-item
              label="token"
              :label-col="formItemLayout.labelCol"
              :wrapper-col="formItemLayout.wrapperCol"
            >
              <a-input
                placeholder="请输入token数量"
                :value="currentSelect.token"
                @change="tokenChange"
              />
			
            </a-form-item>
            <a-form-item
              label="类型"
              :label-col="formItemLayout.labelCol"
              :wrapper-col="formItemLayout.wrapperCol"
            >
              <a-input
                :value="currentSelect.nodeName"
                placeholder="节点类型"
                disabled
              />
            </a-form-item>
          </a-form>
        </template>
      </a-tab-pane>
      <a-tab-pane key="tran-attr">
        <span slot="tab">
          <a-icon type="profile" />
          变迁属性
        </span>
        <template v-if="currentSelect.type == 'transaction'">
          <a-form layout="horizontal">
            <a-form-item
              label="类型"
              :label-col="formItemLayout.labelCol"
              :wrapper-col="formItemLayout.wrapperCol"
            >
              <a-tag color="purple">{{ currentSelect.type }}</a-tag>
            </a-form-item>
            <a-form-item
              label="id"
              :label-col="formItemLayout.labelCol"
              :wrapper-col="formItemLayout.wrapperCol"
            >
              <a-input :value="currentSelect.id" disabled />
            </a-form-item>
          </a-form>
        </template>
      </a-tab-pane>
      <a-tab-pane key="link-attr">
        <span slot="tab">
          <a-icon type="branches" />
          连线属性
        </span>
        <a-form layout="horizontal">
          <a-form-item
            label="id"
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
          >
            <a-input :value="currentSelect.id" disabled />
          </a-form-item>
          <a-form-item
            label="源节点"
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
          >
            <a-input :value="currentSelect.sourceId" disabled />
          </a-form-item>
          <a-form-item
            label="目标节点"
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
          >
            <a-input :value="currentSelect.targetId" disabled />
          </a-form-item>
          <a-form-item
            label="弧权值"
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
          >
            <a-input :value="currentSelect.weight" @change="linkWeightChange" />
          </a-form-item>
        </a-form>
      </a-tab-pane>
    </a-tabs>
  </div>
</template>

<script>
import jsplumb from "jsplumb";

export default {
  props: ["plumb", "flowData", "select"],
  components: {
    jsplumb
  },
  data() {
    return {
      currentSelect: this.select,
      formItemLayout: {
        labelCol: { span: 6 },
        wrapperCol: { span: 16 }
      },
      activeKey: "flow-attr",
      flow: this.flowData
    };
  },
  methods: {
    tokenChange(e) {
      const that = this;
	  that.currentSelect.token=e.target.value ;
    },
    linkWeightChange(e) {
      const that = this;
      let weight = e.target.value;

      that.currentSelect.weight = weight;
      let conn = that.plumb.getConnections({
        source: that.currentSelect.sourceId,
        target: that.currentSelect.targetId
      })[0];
      if (weight != "") {
        conn.setLabel({
          label: weight,
          cssClass: "linkLabel"
        });
      } else {
        let labelOverlay = conn.getLabelOverlay();
        if (labelOverlay) conn.removeOverlay(labelOverlay.id);
      }
    },
    petriDesChange(e) {
      this.flow.attr.des = e.target.value;
    },
    petriNameChange(e) {
      this.flow.attr.name = e.target.value;
    }
  },
  watch: {
    select(val) {
      this.currentSelect = val;
      if (!this.currentSelect.type) {
        this.activeKey = "flow-attr";
      } else if (this.currentSelect.type == "link") {
        this.activeKey = "link-attr";
      } else if (this.currentSelect.type == "transaction") {
        this.activeKey = "tran-attr";
      } else {
        this.activeKey = "node-attr";
      }
    },
    currentSelect: {
      handler(val) {
        this.$emit("update:select", val);
      },
      deep: true
    },
    flow: {
      handler(val) {
        this.$emit("update:flowData", val);
      },
      deep: true
    }
  }
};
</script>

<style lang="scss">
@import "../style/flow-attr.scss";
</style>



