<template>
  <div id="alg">
    <a-tabs size="small" defaultActiveKey="alg" :activeKey="activeKey">
      <a-tab-pane key="alg">
        <span slot="tab" @click="selectAlg">
          <a-icon type="calculator" />
          算法
        </span>
        <a-button id="algBtn" type="primary" @click="showReach">
          <a-icon type="share-alt" /> Reachable graph
        </a-button>
        <a-button id="algBtn" type="primary" @click="showSimphon">
          <a-icon type="share-alt" /> Simphon
        </a-button>
        <a-button id="algBtn" type="primary" @click="showEventWait">
          <a-icon type="share-alt" /> EventWait
        </a-button>
      </a-tab-pane>
      <a-tab-pane key="admin">
        <span slot="tab" @click="selectAdmin">
          <a-icon type="user" />
          用户
        </span>
      </a-tab-pane>
    </a-tabs>

    <a-modal
      :title="ansTitle"
      :visible="ansVisible"
      @cancel="closeAns"
      :footer="null"
    >
      <a-textarea
        :rows="16"
        placeholder="The results will be shown here."
        :value="resultInfo"
      />
    </a-modal>

    <a-modal
      title="Generate reachable graph"
      :visible="reachVisible"
      @ok="generateReachGraph"
      @cancel="closeReach"
    >
      <a-input
        v-model.number="params.step"
        placeholder="Please enter number of transmit steps"
      />
    </a-modal>

    <a-modal
      title="Generate simphon"
      :visible="simphonVisible"
      @ok="generateSimphon"
      @cancel="closeSimphon"
    >
    </a-modal>

    <a-modal
      title="Generate eventWait"
      :visible="eventWaitVisible"
      @ok="generateEventWait"
      @cancel="closeEventWait"
    >
    </a-modal>
  </div>
</template>
<script>
import axios from "axios";
import { flowConfig } from "../config/args-config.js";
export default {
  props: ["flowData", "placeId", "tranId"],
  data() {
    return {
      ansTitle: "",
      activeKey: "alg",
      params: {
        step: null,
        placeCount: null,
        tranCount: null
      },
      resultInfo: "",
      ansVisible: false,
      reachVisible: false,
      simphonVisible: false,
      eventWaitVisible: false,
      netVisible: false,
      spinning: false
    };
  },
  methods: {
    showReach() {
      this.reachVisible = true;
    },
    showSimphon() {
      this.simphonVisible = true;
    },
    showEventWait() {
      this.eventWaitVisible = true;
    },

    closeAns() {
      this.ansVisible = false;
      this.resultInfo = "";
    },
    closeReach() {
      this.reachVisible = false;
    },
    closeSimphon() {
      this.simphonVisible = false;
    },
    closeEventWait() {
      this.eventWaitVisible = false;
    },
    generateReachGraph() {
      this.getRes("generateReachGraph", this.params);
      this.reachVisible = false;
      this.ansTitle = "ReachGraph";
    },

    generateSimphon() {
      this.getRes("generateSimphon");
      this.simphonVisible = false;
      this.ansTitle = "Simphon";
    },
    generateEventWait() {
      this.getRes("generateEventWait");
      this.eventWaitVisible = false;
      this.ansTitle = "EventWait";
    },
    selectAlg() {
      this.activeKey = "alg";
    },
    selectAdmin() {
      this.activeKey = "admin";
    },

    getRes(methodName, params) {
      const that = this;
      let petriData = Object.assign({}, that.flowData);
      petriData.linkList.forEach(link => delete link.cls);
      petriData.attr.maxPlaceId = that.placeId - 1;
      petriData.attr.maxTranId = that.tranId - 1;
      petriData.status = flowConfig.flowStatus.SAVE;
      axios({
        contentType: "application/json;charset=UTF-8",
        method: "post",
        url: flowConfig.serverConfig.algUrl,
        data: {
          methodName,
          nodeList: petriData.nodeList,
          linkList: petriData.linkList,
          status: petriData.status,
          attr: petriData.attr,
          params
        }
      }).then(function(response) {
        let resData = response.data;
        if (resData.success) {
          that.resultInfo = resData.data;
        } else {
          that.$message.error(
            "错误信息：" +
              resData.errorMessage +
              "  " +
              "错误码：" +
              resData.errorCode
          );
        }
      });
    }
  },
  watch: {
    resultInfo(val) {
      if (val) {
        this.ansVisible = true;
      }
    }
  }
};
</script>

<style lang="scss">
#algBtn {
  margin-left: 10px;
  margin-top: 10px;
}
.submit {
  position: "absolute";
  right: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid #e9e9e9;
  padding: 10px 16px;
  background: #fff;
  text-align: right;
  z-index: 1;
}
</style>



